.signupCont {
    width: '80%';
    margin: '0 auto';
  }
 form {
    padding: '20px';
    background-color: '#f9f9f9';
    border: '1px solid #ccc';
    border-radius: '5px';
  }
  label {
    font-weight: 'bold';
    color: '#333';
  }
  input {
    width: '100%';
    padding: '10px';
    margin-top: '10px';
    border: '1px solid #ccc';
    border-radius: '5px';
    font-size: '16px';
  },
  button {
    width: '100%';
    padding: '10px';
    margin-top: '20px';
    background-color: '#333';
    color: '#fff';
    border: 'none';
    border-radius: '5px';
    font-size: '16px';
    cursor: pointer;
  }