@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap');


.background_container{
    width:100vw;
    max-width:100%;
    padding: 0;
    margin: 0;
    border: 0;
    margin-top: 0px;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    
    
}
.image_container{
     width: 100%;
    position: static;
    height: 100%;

}
.background_photo{
    flex-basis: fit-content;
    filter:blur(5px);
}

.quote {
    animation: slide-in 5s ease-out;
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    animation-fill-mode: forwards;  
    font-family: 'Dancing Script', cursive;
    text-shadow: -0.5px 0 rgba(255, 255, 255, 0.377), 0 0.5px rgba(255, 255, 255, 0.377), 1px 0 rgba(255, 255, 255, 0.377), 0 -1px rgba(255, 255, 255, 0.377);

  }
  .quote h1{
    text-align: left;
    margin-right: 5vw;
    color:black;
  }
  .quote p {
    color:black;
    font-size: 2rem;
  }
  .next_course::before {
    content: '';
    background: #301c1a;
    position: absolute;
    width: 110%;
    height: 110%;
    left: 0;
    top: 0;
    z-index: 1;
    animation: lr-bar 750ms;
    animation-fill-mode: both;
    animation-timing-function: cubic-bezier(0.500, 0.250, 0.500, 0.750);
  }
  .next_course{
    position: absolute;
    top:10%;
    left: 5%;
    text-align: left;
    font-family: 'Noto Serif', sans-serif;

  }

  

  .next_course_question{
    font-weight: 700;
    font-size: 48px;
    color:black;
  }

  .next_course_time{
    font-family: 'Noto Serif';
    color:black;
  }
  .course_link_cont{
    animation: pulse 10s infinite;
    
  }
  .course_link{
    background-color:#b1a8a898;
    border-radius: 5px;
    font-size: 40px;
    color:red;
    margin: 20px;
    
    
  }
  .course_link:hover{
    color:white;
  }
  .name{
    text-align: right;
  }

  h1,p{
    color:rgba(255, 255, 255, 0.76);
  }
@keyframes slide-in {
    from {
      transform: translateX(-50%);
    }
    to {
      transform: translateX(0);
    }
  }


  @keyframes pulse {
    0%{
      transform:scale(100%);
    }
    50%{
      transform:scale(105%);
    }
    100%{
      transform:scale(100%);
    }
  }
  
  @keyframes lr-bar {
    0% { transform-origin: center left 0px; transform: scaleX(0); }
    36% { transform: scaleX(1); transform-origin: center left 0px; }
    54% { transform: scaleX(1); transform-origin: center right 0px; }
    90% { transform: scaleX(0);  transform-origin: center right 0px; }
    100% { transform: scaleX(0);  transform-origin: center right 0px; }
  }

  @media (max-width: 767px){
    .quote{
      font-size: 10px;
      left:30%;
      top:60%;
    }
    .next_course_question{
      font-weight: 200;
      font-size:30px;
    }
    .next_course{
      top:20%;
      text-align: center;
    }
    .background_container{
      background-position-x:25%;
      margin-top: 0px;
      height: 100vh;
      width:100vw;
      max-width:100%;
    }
  }

